import angular from 'angular'
import { userListsSelector } from '@app/services/userList/selectors'

export default angular
  .module('ListsControllerModule', [])
  .controller(
    'ListsController',
    function (
      $ngRedux,
      $rootScope,
      $scope,
      $state,
      $timeout,
      config,
      list,
      listPrintService,
      utilService
    ) {
      $scope.deleteList = list.remove
      $scope.loadList = list.loadListToCart
      $scope.listPrintService = listPrintService

      const mapStateToController = state => {
        $scope.lists = userListsSelector(state).map(list => angular.copy(list))
        return {}
      }
      $ngRedux.connect(mapStateToController)(this)

      $scope.showCreateList = function () {
        $scope.creatingList = true
        utilService.focusSelector('.new-list-name')
      }

      $scope.createList = function (listName) {
        if (listName) {
          list.create(listName)

          delete $scope.newListName
          $scope.creatingList = false
        }
      }
    }
  )
